<template>
    <div class="myOrder" ref="myOrder">
        <!-- 头部 -->
         <van-nav-bar
            title="我的订单"
            left-arrow
            @click-left="goBack('Me')"
            />

        <!-- 订单状态 -->
        <van-tabs v-model="attmals" animated  >
            <van-tab v-for="(Item,index) in Liatas" :key="index" :title="Item.name" @change="ff_status">
                 
                 <div class="Bodys">
                     <!--   -->
                     <div class="dart" v-for="(obj,xx) in nowObj" :key="xx">
                         <p class="herds">
                            <span>外卖</span>
                            <span>{{obj[1] | ff_dizhi}}</span>
                            <span>{{obj[2] | ff_state}}</span>
                            <span v-if="obj[2] == 2"  class="iconfont icon-lajitong" @click="ff_rovem(obj[3])"></span>
                         </p>
                        
                        <div   v-for="(val,key) in  obj" :key="key" >
                            <div class="merch" v-if=" typeof(val) == 'object'">
                                <img :src="val.smallImg"/>
                                <div class="alloc">
                                    <p>{{val.name}}</p>
                                    <p>{{val.rule}}</p>
                                    <span>×{{val.count}}</span>
                                </div>
                            </div>
                        </div>
                        
                         <div class="colook">
                             <span>{{obj[0] | ff_click}}</span>
                             <span>合计:<span>￥{{obj[obj.length-2]}}</span></span>
                             <span>共计{{obj[obj.length-1]}}件商品</span>
                         </div>
                         <div class="affirm" @click="[obj[2] ==1   ? ff_receive(obj[3]) : ff_goPath('Menu')]">
                             {{obj[2] | ff_states}}
                         </div>

                     </div>
                     
                 </div>
                 


            </van-tab>
        </van-tabs>


        <!-- 底部加载中 -->
        <van-loading v-if="isLoading"  size="24px">{{text}}</van-loading>
       
       <van-divider v-if="isDivider" dashed>我也是有底线的</van-divider>

    </div>
</template>

<script>
import '../assets/font/iconfont.css'
    import '../assets/less/Order.less'
    export default {
          name:'Order', 
          data() {
              return {
                  Liatas : [
                      {name : "全部订单",statusCode : 0},
                      {name : "待收货"  ,statusCode : 1},
                      {name : "已收货"  ,statusCode : 2},
                  ],
                  attmals   :  '',
                  Dates     : [],
                  price     : 0,
                  cuout     : 0,
                  newObj    : {},
                  nowObj    : {},
                  arrObj    : [],
                  sum       : 3,
                  isLoading : true,
                  isDivider : false,
                  text      : '加载中...'

              }
          },
          methods: {
              
              goBack(name){
                  window.removeEventListener('scroll',this.ff_di);
                  setTimeout(()=>{
                        this.$router.push({name})
                  },500)
                 
                 
                  
              },
            //   查询订单
              ff_status(){
                        this.axios({
                            url:'/findOrder',
                            method:'GET',
                            params:{
                                appkey : this.appkey,
                                tokenString : this.useId,
                                status  : this.attmals,
                            }
                        })
                        .then(res=>{
                            if(res.data.code == 70000){
                                this.Dates ;
                                let a = res.data.result;
                                let b = {};
                                
                                

                            //    
                                // 分订单
                                a.forEach(Item=>{
                                    if(!b[Item['oid']]){
                                        b[Item['oid']] = [];
                                        b[Item['oid']].push(Item.updatedAt);
                                        b[Item['oid']].push(Item.address);
                                        b[Item['oid']].push(Item.status);
                                        b[Item['oid']].push(Item.oid);
                                    }
                                });
                                a.forEach(Item=>{
                                    for(let key in b){
                                         if(Item.oid == key){
                                               b[key].push(Item);
                                                
                                         } 
                                        
                                    }  
                                                                  
                                });
                                for(let key in b){
                                    let price = 0;
                                    let cuout = 0;
                                    b[key].forEach((Item,index)=>{
                                        if(typeof(Item) == 'object'){
                                                price += (Item.price-0-16) * (Item.count-0);
                                                cuout += (Item.count-0);  
                                               
                                        }
                                        
                                    })
                                     b[key].push(price+6);
                                     b[key].push(cuout);  
                                }
                              
                                this.Dates   = a;
                                this.newObj  = b;  
                                this.newObj  = this.ff_sortObj(this.newObj);
                                this.nowObj  = this.ff_slice(this.newObj);
                                
                           
                                this.price = this.newObj[-2];
                                this.cuout = this.newObj[this.newObj.length-1]
                            }
                        })
                        .catch(err=>{
                             
                        })
              },
            //   确认收货
              ff_receive(oid){
                   this.axios({
                       url:'/receive',
                       method:'POST',
                       data:{
                            appkey: this.appkey,
                            tokenString: this.useId,
                            oid,
                       }
                   })
                   .then(res=>{
                       if(res.data.code == 80000){
                               this.$toast(res.data.msg);
                               this.ff_status();  
                       }
                   })
                   .catch(err=>{
                        
                   })
              },
              ff_goPath(name){
                  this.$router.push({name})
              },
            //   移除订单信息
              ff_rovem(oid){
                  this.$dialog.confirm({
                    message: '确认移除订单信息',
                    })
                    .then(() => {
                            this.axios({
                                url:'/removeOrder',
                                method:'POST',
                                data:{
                                        appkey: this.appkey,
                                        tokenString: this.useId,
                                        oid
                                }
                            })
                            .then(res=>{
                                   
                                if(res.data.code == 90000){
                                    this.$toast(res.data.msg);
                                    this.ff_status();
                                }
                            })
                            .catch(err=>{
                                 
                            })
                    })
                    .catch(()=>{
                         
                    })
                   
              },
            //   实现对象属性的倒序
              ff_sortObj(obj) {
                    var arr = [];
                    for (var i in obj) {
                        arr.push([obj[i],i]);
                    };
                    arr.reverse();
                    this.arrObj = arr;
                    var len = arr.length;
                    var obj = {};
                    for (var i = 0; i < len; i++) {
                        obj[arr[i][1]] = arr[i][0];
                    }
                    return obj;
              },
            // 截取对象的前俩位属性
              ff_slice(obj,sum=3){
                var arr = [];
                for (var i in obj) {
                    arr.push([obj[i],i]);
                };
                arr = arr.slice(0,sum);
                var len = arr.length;
                var obj = {};
                for (var i = 0; i < len; i++) {
                    obj[arr[i][1]] = arr[i][0];
                }
                
                if(sum >= this.arrObj.length){
                     this.isDivider = true;
                    
                }else{
                    this.isLoading  =  false;
                }
                
                return obj;
              },
            // 触底加载
              ff_di(e){
                  let bottoms = this.$refs.myOrder.offsetHeight - window.innerHeight;
                  let mybotms = Math.ceil(e.path[1].pageYOffset);
                  if(mybotms >= bottoms){
                  
                   
                    if(this.isDivider){
                            this.isLoading = false;
                            
                    }else{
                        this.isLoading =  true;
                    
                        var time = setTimeout(()=>{
                            this.sum       =  this.sum+2;
                            this.sum       =  this.sum >= this.arrObj.length ?  this.arrObj.length : this.sum;
                            
                            this.nowObj    =  this.ff_slice(this.newObj,this.sum);
                        },1500);

                    }
                    
                }
              }
             
              
          },
          watch: {
              attmals(val,old){
                   this.attmals = val;
                    this.ff_status();  
                    this.isDivider = false;
                  
              }
          },
          created() {
            
              this.ff_status();  
            
          },
          mounted(){
              //   触底加载
            window.addEventListener('scroll',this.ff_di)

          },
          filters:{
              ff_state(item){
                 let statuStr = item == 1 ? '待收货' : '已收货' ;
                 return statuStr
              },
              ff_states(item){
                  let statuStr = item == 1 ? '确认收货' : '再来一单' ;
                  return statuStr
              },
              ff_dizhi(item){
                  let statuStr = item == 'undefined' ? '北京市朝阳区朝阳大街660号' : item ;
                   return statuStr
              },
              ff_click(item){
                 
                   let statuStr =  '';
                   let deat   =  item.split('T')[0];
                   let time   =  item.split('T')[1].slice(0,5).split(':');
                   let clicks =  time[0];
                   if(clicks.slice(0,1) == 0){
                         clicks = clicks.slice(1,2)-0 + 8;
                   }else if(clicks.slice(0,1) == 1){
                         clicks = clicks.slice(0,2)-0 + 8;
                         if(clicks >= 24 ){
                             clicks  = clicks - 24;
                         }
                   }
            
                   statuStr = `${deat} ${clicks}:${time[1]}`
                   return statuStr
              }
          },
         
          

          
          
    }
</script>

<style lang="less" scoped>

</style>